.movies .movie {
    background-color : white;
    margin-bottom : 70px;
    font-weight : 300;
    padding : 20px;
    border-radius: 5px;
    color : 3adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}
.movies .movie a {
    display : grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap : 20px;
    text-decoration: none;
    color : inherit;
}
.movie img {
    position : relative;
    top : -50px;
    max-width: 150px;
    width : 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}
.movie .movie__title,
.movie .movie__year {
    margin : 0;
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom : 5px;
    font-size : 24px;
    color : #2c2c2c;
}
.movie .movie__genres { 
    list-style:none;
    padding: 0;
    margin : 0;
    display : flex;
    flex-wrap : wrap;
    margin : 5px 0px;
}
.movie__genres li,
.movie .movie__year {
    margin-right:10px;
    font-size:14px;
}


















.container {
    height : 100%;
    display : flex;
    justify-content: center;
}
.loader {
    width : 100%;
    height : 100vh;
    display : flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}
.movies { 
    display : grid;
    grid-template-columns: repeat(2,minmax(400px,1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}
@media screen and (max-width:1090px) {
    .movies {
        grid-template-columns: 1fr;
        width:100%;
    }
}
.nav {
    z-index:1;
    position: fixed;
    top: 50px;
    left: 10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding:10px 20px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
    border-radius:5px;
}

@media screen and (max-width: 1090px) {
    .nav {
        left:auto;
        left:initial;
        top:auto;
        top:initial;
        bottom:0px;
        width:100%;
    }
}
.nav a {
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}
.nav a:not(:last-child) {
    margin-bottom: 20px;
}
















* {
    box-sizing: border-box;
}

body {
    margin : 0;
    padding : 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height:100%;
}
.detail {
    background-color : white;
    margin : 45px 40px 0px;
    padding : 30px;
    font-weight : 400;
    border-radius: 5px;
    color : 3adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}
.detail img {
    position : relative;
    top : -40px;
    left : 25px;
    max-width: 250px;
    width : 100%;
