    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}
.detail .detail__title,
.detail .detail__year {
    margin : 0;
    font-weight: 600;
}

.detail .detail__title {
    margin-bottom : 5px;
    font-size : 24px;
    color : #2c2c2c;
}
.detail .detail__genres { 
    list-style:none;
    padding: 0;
    margin : 0;
    display : flex;
    flex-wrap : wrap;
    margin : 5px 0px;
    flex-direction: row;
}
.detail__genre {
    padding-left : 12px;
